import React, { Component, Fragment } from 'react';

import { Card, Row, Table, Popconfirm, Col } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';

import { enrollmentPlan } from 'dump/data';

class ListEnrollmentPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.columns = [
      {
        title: 'M phng n',
        dataIndex: 'code',
        key: 'trainingIndustryCode',
      },
      {
        title: 'Tn phng n',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Khu vc tuyn sinh',
        dataIndex: 'zones',
        key: 'nameEn',
      },
      {
        title: 'H',
        dataIndex: 'he',
        key: 'nameEn',
      },
      {
        title: 'im tng',
        dataIndex: 'total',
        key: 'nameEn',
      },
      {
        title: 'im chi tit',
        dataIndex: 'detail',
        key: 'nameEn',
      },
      {
        title: 'S lng tuyn sinh',
        dataIndex: 'number',
        key: 'nameEn',
      },

      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) =>
          enrollmentPlan.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#/enrollment-plan/form/edit/${record.code}`} />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
  }

  render() {
    return (
      <Card title="Phng n tuyn sinh">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/enrollment-plan/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={enrollmentPlan} columns={this.columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListEnrollmentPlan />
  </Page>
);



